import React from 'react';
import { useNavigate } from 'react-router-dom';
import './Iban.css';

function Iban() {
  const navigate = useNavigate();

  return (
    <div className="iban-container">
      <div className="iban-content">
        <h2>Viaggio di Nozze</h2>
        <p>
          Il pensiero più significativo che potreste regalarci è la vostra presenza al nostro giorno speciale. Tuttavia, se desiderate farci un dono, mettetelo nel nostro salvadanaio, diventando parte dei ricordi felici che ci accompagneranno nel nostro futuro insieme.
        </p>
        <p>
          <strong>IBAN:</strong> IT59 C360 81 051 382 340 974 34 137 
        </p>
        <p>
          <strong>Intestatario:</strong> Leonardo Fogliani
        </p>
        <p>
          <strong>Causale:</strong> Regalo matrimonio + "Inserire proprio nome"
        </p>
        <p>Grazie, se contribuirete a rendere il nostro viaggio unico.</p>

        <button className="home-button" onClick={() => navigate('/')}>
          Torna alla Home
        </button>
      </div>
    </div>
  );
}

export default Iban;
